@import "vars";


/*==========  Desktop First  ==========*/

// ≥1200px
@include media-breakpoint-down(lg);

// ≥992px
@include media-breakpoint-down(md) {
	.nav-list_link {
		font-size: 1.1rem;
		padding: {
			left: .5rem;
			right: 0.5rem; }
		&.is-active {
			font-size: 1.2rem; } }
	.index {
		aside {
			margin: {
				left: auto;
				right: auto; } }
		.content {
			display: none; } }
	aside {
		max-width: 50%!important;
		flex: 0 0 50%!important; }
	.content {
		max-width: 50%;
		flex: 0 0 50%; }
	.description {
		padding: 2rem;
		width: 100%;
		&-title {
			font-size: 3.2rem; } }
	.checkout {
		display: none;
		width: 95vw;
		position: relative;
		top: auto;
		left: auto;
		height: auto;
		&-close, &-edit {
			display: none; }
		&.fancybox-content {
			opacity: 1;
			visibility: visible;
			pointer-events: auto; } }
	.map {
		padding: 0;
		display: none;
		position: relative;
		top: auto;
		left: auto;
		opacity: 1;
		visibility: visible;
		pointer-events: auto;
		@include size(95vw,95vh); }
	.is-checkout {
		.form-wrapper:after {
			display: none; } }
	.is-scrolled {
		.section {
			padding-bottom: 3rem;
			&:not(.index) {
				padding: {
					top: 3rem; } } } } }
// ≥768px
@include media-breakpoint-down(sm) {
	aside {
		max-width: 100%!important;
		flex: 0 0 100%!important; }
	.content {
		max-width: 100%;
		flex: 0 0 100%;
		margin-bottom: 3rem; }
	.description {
		padding: 0;
		&-title {
			text-align: center;
			strong {
				display: inline; } } }
	.header {
		position: relative;
		padding: {
			top: 1rem;
			bottom: 1rem; }
		&-logo a {
			display: block;
			width: 17rem;
			img {
				max-width: 100%;
				display: block; } } }
	.nav {
		position: absolute;
		top: 100%;
		width: 100%;
		transform: translateX(-100%);
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		transition: 0.4s;
		margin-top: 0;
		z-index: 2; }
	.index {
		padding-top: 10.3rem; }
	.is-menu {
		.nav {
			transform: translateX(0);
			opacity: 1;
			visibility: visible;
			pointer-events: auto; } }
	.table-wrapper {
		overflow-x: auto; } }
// ≥576px
@include media-breakpoint-down(xs) {
	.row-xl, .row-l {
		margin: {
			left: -1rem;
			right: -1rem; }
		> * {
			padding: {
				left: 1rem;
				right: 1rem; } } } }
// ≥410px
@include media-breakpoint-down(xxs) {
	.header {
		&-login_link {
			font-size: 1.2rem; }
		&-logo a {
			width: 13rem; } }
	.index {
		padding-top: 6.2rem; }
	.form-wrapper {
		padding: 1.5rem; }
	.checkout {
		padding: 2.5rem .5rem; } }
/*==========  Mobile First  ==========*/

// ≤576px
@include media-breakpoint-up(sm);

// ≤768px
@include media-breakpoint-up(md);

// ≤992px
@include media-breakpoint-up(lg);

// ≤1200px
@include media-breakpoint-up(xl);
