@import "vars";
@import "libs";

::placeholder {
	color: #999999; }
html, body {
	height: 100%; }
.main {
	flex: 1 0 auto; }
html {
	font-size: 10px; }
body {
	background-color: $text;
	font-size: 1.4rem;
	min-width: 320px;
	position: relative;
	line-height: 1.5;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	display: flex;
	flex-direction: column;
	input, textarea {
		display: block;
		width: 100%;
		outline: none;
		resize: none;
		border: none; }
	input, textarea, button, a {
		&:active, &:hover, &:focus {
			outline: 0;
			outline-offset: 0; } }
	button {
		cursor: pointer;
		background-color: transparent;
		border: none;
		padding: 0; }
	&.profile {
		background-color: #fff; } }
b, strong {
	font-weight: 700; }
p {
	margin-bottom: 0; }
a {
	color: $text;
	text-decoration: none;
	transition: 0.25s;
	&:hover {
		color: inherit;
		text-decoration: underline; }
	&[href^="#"], &[href^="tel"], &[href^="mail"] {
		&:hover {
			text-decoration: none; } } }
.w-100 {
	width: 100%; }
.h-100 {
	height: 100%; }
.list {
	&-unstyled {
		@include list-unstyled; } }
.icon {
	display: inline-block;
	vertical-align: middle;
	top: -0.3rem;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	&-add {
		@include size(2rem);
		background-image: url(../img/icon/icon-add.png); }
	&-doc {
		@include size(2rem, 2.6rem);
		background-image: url(../img/icon/icon-doc.png); }
	&-edit {
		@include size(2rem);
		background-image: url(../img/icon/icon-edit.png); }
	&-left {
		margin-right: 1rem; } }
.image {
	display: block;
	background-position: center;
	background-repeat: no-repeat;
	position: relative;
	&-con {
		background-size: contain; }
	&-cov {
		background-size: cover; }
	&-round {
		border-radius: 0.9rem; } }
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	display: block;
	margin-bottom: 0;
	line-height: 1.2;
	font-weight: 500; }
h1, .h1 {
	font-size: 2.1rem; }
h2, .h2 {
	font-size: 1.9rem; }
h3, .h3 {
	font-size: 1.7rem; }
h4, .h4 {
	font-size: 1.5rem; }
h5, .h5 {
	font-size: 1.4rem; }
.text {
	&-xs {
		font-size: 1rem; }
	&-s {
		font-size: 1.2rem; }
	&-m {
		font-size: 1.4rem; }
	&-l {
		font-size: 1.6rem; }
	&-xl {
		font-size: 1.8rem; } }
.btn {
	display: flex;
	cursor: pointer;
	transition: 0.25s;
	justify-content: center;
	align-items: center;
	text-decoration: none!important;
	&-xxs {}
	&-xs {}
	&-s {}
	&-m {
		height: 4.4rem;
		text-transform: uppercase;
		font-size: 1.2rem;
		font-weight: 700;
		padding: {
			left: 2.5rem;
			right: 2.5rem; } }
	&-l {
		height: 4.6rem;
		font-size: 1.4rem;
		padding: {
			left: 2.45rem;
			right: 2.45rem; }
		&.btn-round {
			border-radius: 2.3rem; } }
	&-red {
		background-color: $accent;
		color: #ffffff;
		&:hover {
			color: #ffffff;
			background-color: darken($accent, 10%); } }
	&-black {
		background-color: $text;
		color: #ffffff;
		&:hover {
			color: #ffffff;
			background-color: lighten($text, 10%); } }
	&-gray {
		background-color: #f7f7f7;
		color: #000;
		&:hover {
			color: #000;
			background-color: darken(#f7f7f7, 10%); } }
	.icon-add {
		margin-left: -1rem; } }
.hidden {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	pointer-events: none; }
.row {
	&-xxs {
		@include make-row(.6rem);
		> * {
			padding: {
				left: .3rem;
				right: .3rem; } } }
	&-xs {
		@include make-row(1rem);
		> * {
			padding: {
				left: .5rem;
				right: .5rem; } } }
	&-s {
		@include make-row(2rem);
		> * {
			padding: {
				left: 1rem;
				right: 1rem; } } }
	&-l {
		@include make-row(3.2rem);
		> * {
			padding: {
				left: 1.6rem;
				right: 1.6rem; } } }
	&-xl {
		@include make-row(5rem);
		> * {
			padding: {
				left: 2.5rem;
				right: 2.5rem; } } } }
.form {
	display: block;
	&-group {
		position: relative;
		&:not(:first-child) {
			margin-top: 1.3rem; } }
	&-control {
		display: block;
		width: 100%;
		height: 3rem;
		line-height: 3rem;
		color: #ffffff;
		border-bottom: .1rem solid rgba(#fff, .5);
		transition: 0.25s;
		font-size: 1.4rem;
		background-color: transparent;
		&:focus {
			border-color: #fff; } }
	&-date {
		background: url(../img/data/form-calend.png) right center no-repeat;
		cursor: pointer; }
	&-label {
		display: block;
		color: #aaaaaa;
		font-size: 1.2rem;
		margin-bottom: 0; }
	&-submit {
		margin-top: 2.8rem;
		.btn {
			margin: 0 auto; } }
	&-info {
		margin-top: 3.2rem;
		&_text {
			display: block;
			text-align: center;
			font-size: 1.2rem;
			color: #aaaaaa;
			strong {
				font-size: 1.6rem;
				font-weight: 700;
				color: #ffffff; } } }
	&-radio, &-checkbox {
		@extend .hidden; }
	&-radio {
		&:checked + .form-label {
			background-color: $accent;
			border-color: $accent;
			color: #ffffff; }
		+ .form-label {
			display: block;
			color: #ffffff;
			font-size: 1.4rem;
			font-weight: 700;
			text-transform: uppercase;
			height: 3.6rem;
			line-height: 3.6rem;
			border: 0.1rem solid #a4a4a4;
			text-align: center;
			cursor: pointer; } }
	&-code {
		display: block;
		color: #d50707;
		text-decoration: underline;
		cursor: pointer;
		margin-top: .8rem;
		&:hover {
			text-decoration: none; } } }
// section
.section {
	min-height: 100vh;
	padding: {
		top: 21.4rem; } }
// section

// scrolled
body:not(.profile) {
	&.is-scrolled {
		.header {
			position: absolute; }
		.footer {
			position: relative;
			top: auto;
			left: auto;
			right: auto;
			bottom: auto;
			padding: 2.4rem 0; }
		.header {
			margin-bottom: {} }
		.section {
			min-height: auto;
			padding-bottom: 5rem;
			&:not(.index) {
				padding: {
					top: 5rem; } } } } }
// scrolled

// header
body:not(.profile) {
	.header {
		color: #ffffff;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 2;
		&-contact {
			&_link {
				color: #ffffff; } }
		&-login {
			&_link {
				color: #ffffff; } } } }
.header {
	padding: {
		top: 2.5rem; } }
.profile {
	.header {
		margin-bottom: 3rem; } }
// header

// nav
.nav {
	margin-top: 1.8rem;
	background-color: $accent;
	color: #ffffff;
	padding: {
		top: 2.2rem;
		bottom: 2.1rem; }
	&-list {
		&_link {
			display: block;
			font-size: 1.2rem;
			text-transform: uppercase;
			font-weight: 700;
			color: #ffffff;
			padding: .6rem 1rem;
			border-radius: 0.3rem;
			&:hover, &.is-active {
				text-decoration: none;
				background-color: $text; }
			&.is-active {
				font-size: 1.3rem; } } } }
// nav

// map
.map {
	opacity: 0;
	transition: 0.4s;
	pointer-events: none;
	visibility: hidden;
	@include size(100%);
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;
	iframe {
		@include size(100%); }
	&.is-show {
		opacity: 1;
		visibility: visible;
		pointer-events: auto; } }
// map

// checkout
.is-checkout {
	.form-wrapper:after, .checkout {
		opacity: 1;
		visibility: visible;
		pointer-events: auto; } }
.index {
	.checkout {
		position: relative;
		left: auto;
		top: auto; } }
.checkout {
	position: absolute;
	top: 0;
	left: 0;
	@include size(100%);
	background-color: #ffffff;
	display: flex;
	padding: 2.5rem;
	flex-direction: column;
	justify-content: center;
	opacity: 0;
	transition: 0.4s;
	visibility: hidden;
	pointer-events: none;
	z-index: 2;
	> *:not(button) {
		width: 100%;
		margin: 0; }
	&-close {
		@include size(3rem);
		background: url(../img/data/checkout-close.png) center no-repeat;
		opacity: 0.8;
		z-index: 2;
		&:hover {
			opacity: 1; }
		position: absolute;
		top: 2rem;
		right: 2rem; }
	&-title {
		font-size: 2.4rem;
		font-weight: 700;
		line-height: 1;
		text-transform: uppercase;
		display: block;
		margin-bottom: 1.1rem; }
	&-message {
		display: block;
		margin-bottom: 3.2rem; }
	.form {
		&-group {
			&:not(:first-child) {
				margin-top: 1.9rem; } }
		&-label {}
		&-control {
			border-color: rgba(#050505, .2);
			color: #050505;
			height: 2.2rem;
			line-height: 2.2rem;
			&:focus {
				border-color: #050505;
				color: #050505; } } }
	.selectric {
		border-bottom: .1rem solid rgba(#050505, .2);
		&-below .selectric .label, .label {
			color: #050505; }
		.button:after {
			border-top-color: #050505; } }
	&-detail {
		background-color: #ebebeb;
		padding: 2.8rem 2rem;
		margin-top: 2rem;
		margin-bottom: 2rem; }
	&-subtitle {
		display: block;
		font-size: 2rem;
		font-weight: 500; }
	&-edit {
		display: block;
		cursor: pointer;
		color: #d50707;
		text-decoration: underline;
		&:hover {
			text-decoration: none; } }
	&-list {
		margin-top: 2.7rem;
		&_item {
			&:not(:first-child) {
				margin-top: .9rem; } } }
	&-price {
		display: block;
		font-size: 1.8rem;
		font-weight: 700;
		strong {
			color: #d50707; } }
	&-discount {
		display: block; }
	.btn[type="submit"] {
		width: 100%; }
	&-info {
		display: block;
		margin-top: 2.4rem; } }
// checkout

// description
.description {
	position: relative;
	@include size(75%, 100%);
	padding: 2.6rem 7.7rem;
	color: #ffffff;
	&-title {
		display: block;
		text-transform: uppercase;
		font-weight: 300;
		font-size: 5rem;
		line-height: 1;
		margin-bottom: 3.3rem;
		strong {
			display: block;
			font-weight: 700; } }
	p {
		font-size: 1.6rem;
		color: rgba(#fff,.6);
		&:not(:last-child) {
			margin-bottom: 1.5rem; } } }
.contact {
	.description {
		padding: 0;
		width: 100%;
		ul {
			@include list-unstyled; } } }
// description

// form
.form {
	&-title {
		display: block;
		color: #ffffff;
		font-size: 1.8rem;
		font-weight: 700;
		text-transform: uppercase; }
	&-wrapper {
		border: 0.3rem solid #ffffff;
		background-color: rgba(#000000,0.5);
		padding: 3.7rem 3rem 2.6rem;
		position: relative;
		&:after {
			content: '';
			display: block;
			position: absolute;
			@include size(100%);
			left: 0;
			top: 0;
			background-color: rgba(#ebebeb,.64);
			transition: 0.4s;
			opacity: 0;
			visibility: hidden;
			pointer-events: none; } }
	&-password_change {
		font-size: 1rem;
		display: block;
		position: absolute;
		bottom: .5rem;
		right: 0; } }
// form

// footer
.footer {
	position: fixed;
	bottom: 2.4rem;
	left: 0;
	right: 0;
	z-index: 2; }
// footer

// aside
aside {
	max-width: 39rem!important;
	flex: 0 0 39rem!important; }
.content {
	flex-grow: 1;
	flex-shrink: 0;
	flex-basis: calc(100% - 39rem); }
// aside

// popup
.popup {
	background-color: #ffffff;
	padding: 3rem;
	width: 32rem;
	display: none;
	&-title {
		display: block;
		text-align: center;
		font-size: 2rem;
		font-weight: 500;
		margin-bottom: 2rem; }
	.form-control {
		border-color: rgba(#050505, .2);
		color: #050505;
		&:focus {
			border-color: #050505;
			color: #050505; } } }
// popup

// profile
.profile {
	padding-bottom: 5rem;
	&-list {
		margin-bottom: 3rem;
		&_item {}
		&_link {
			font-size: 1.6rem;
			text-transform: uppercase;
			font-weight: 500;
			&:hover {
				text-decoration: none;
				border-bottom: .2rem solid $accent; }
			&.is-active {
				border-bottom: .2rem solid $accent; } } }
	.form-label {
		color: $accent; }
	.form-control {
		border-color: #d8d8d8;
		color: $text;
		font-size: 1.6rem;
		&::placeholder {
			color: $text; } }
	.form-info_text strong {
		color: $text; }
	.selectric {
		border-bottom: .1rem solid #d8d8d8;
		.button:after {
			border-top-color: #d8d8d8; }
		.label {
			color: $text; } }
	.table {
		width: 100%;
		thead {
			tr {
				background-color: #f5f5f5;
				th {
					font-weight: 400;
					font-size: 1.2rem;
					text-align: center;
					padding: 1.4rem 1.7rem;
					&:first-child {
						text-align: left; } } } }
		tbody {
			tr {
				td {
					padding: 0.88rem 1.7rem;
					background-color: #f5f5f5;
					border-top: .1rem solid #fff;
					border-bottom: .1rem solid #fff;
					border-right: .2rem solid #fff;
					border-left: .2rem solid #fff;
					text-align: center;
					vertical-align: middle;
					.info-show {
						position: relative;
						padding-right: 1.4rem;
						&:after {
							content: " ";
							position: absolute;
							top: 0;
							right: 0;
							bottom: 0;
							margin: auto;
							width: 0;
							height: 0;
							border: 4px solid transparent;
							border-top-color: $text;
							border-bottom: none; } } }
				&.table-status {
					td {
						background-color: #ffffff;
						border: none;
						text-align: left;
						padding: {
							top: 1rem;
							bottom: 1rem; } } }
				&.table-expand {
					td {
						padding: 0;
						border: none; } } }
			.table-doc_content {
				display: none;
				position: relative;
				background-color: #fbfbfb;
				text-align: left;
				padding: 1.4rem 2.65rem;
				border-top: .1rem solid #fff;
				border-bottom: .1rem solid #fff;
				a {
					color: $accent; }
				.row {
					margin-top: 1.4rem;
					&:first-child {
						margin-top: 2.3rem; } }
				.doc-close {
					position: absolute;
					top: 1.1rem;
					right: 1.1rem;
					@include size(3.6rem);
					opacity: 0.3;
					transition: 0.25s;
					&:hover {
						opacity: 1; } } }
			.table-info {
				&_content {
					display: none;
					position: relative;
					background-color: #fbfbfb;
					text-align: left;
					padding: .6rem 6rem .6rem 2.65rem;
					border-top: .1rem solid #fff;
					border-bottom: .1rem solid #fff;
					.info-close {
						position: absolute;
						top: 1.1rem;
						right: 1.1rem;
						@include size(3.6rem);
						opacity: 0.3;
						transition: 0.25s;
						&:hover {
							opacity: 1; } }
					.h1 {
						margin: {
							top: 1.4rem;
							bottom: 2.3rem; } }
					.table-map {
						height: 100%;
						iframe {
							width: 100%;
							height: 100%; } } }
				&_detail {
					margin-bottom: 1.4rem;
					> *:not(:first-child) {
						margin-top: 1.3rem; } } } } } }
.client-change {
	color: $accent;
	&:hover {
		color: $accent; } }
.company-add {
	&:hover {
		color: $accent; } }
// profile

@import "media";
